import fs from "fs";
import path from "path";
import matter from "gray-matter";
import { serialize } from "next-mdx-remote/serialize";

export async function getPost(section, slug) {
  const filePath = path.join(process.cwd(), "content", section, `${slug}.md`);
  const source = fs.readFileSync(filePath, "utf8");
  const { data, content } = matter(source);
  const mdxSource = await serialize(content);
  return { frontMatter: data, mdxSource };
}

export function getAllPosts(section) {
  const dirPath = path.join(process.cwd(), "content", section);
  const files = fs.readdirSync(dirPath);
  return files.map((file) => {
    const slug = file.replace(/\.md$/, "");
    const source = fs.readFileSync(path.join(dirPath, file), "utf8");
    const { data } = matter(source);
    return { frontMatter: data, slug };
  });
}
